import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import {searchRecipe, submitSearch, searchTermGiven, submitForm} from '../actions';
class SideMenu extends React.Component {
  render() {
    return (
      <div className="ui inverted menu">
		  <Link to="/" className="active red item">
				<i class="home icon"></i>
		  </Link>
		  <Link to="/search" className="active green item">
				<i class="utensils icon"></i>
		  </Link>
		  <Link to="/bookmarks" className="active blue item">
				<i class="bookmark icon"></i>
		  </Link>
		    <div className="item">
		      <div className="ui transparent icon input">
		        <input type="text" placeholder="Search..."
		        onChange={e=> this.props.searchRecipe(e.target.value)}
				onKeyDown={e=> {
				     if(e.keyCode === 13){
				      	this.props.submitSearch(this.props.searchTerm.Recipe)
				      	this.props.searchTermGiven(this.props.searchTerm.Recipe)
				      	this.props.submitForm(false)
				     }
				}} style={{color: 'snow'}}/>
		        <i 
		        onClick={e=> {
				      	this.props.submitSearch(this.props.searchTerm.Recipe)
				      	this.props.searchTermGiven(this.props.searchTerm.Recipe)
				      	this.props.submitForm(false)
				    }
				}
		        className="inverted search link icon"></i>
		      </div>
		    </div>
		</div>

    )
  }
}

const mapStateToProps = (state) => {
	return {searchTerm: state.search}
}
export default connect(mapStateToProps,{searchRecipe, submitSearch, searchTermGiven, submitForm})(SideMenu);