import React from 'react';
import { connect } from 'react-redux';
import Accordion from './Accordion';
import { Pagination } from 'semantic-ui-react';
import { handlePage, submitParams } from '../actions';
class MealFilter extends React.Component {
	renderCount(){
		if(!this.props.recipes){
			return null;
		}
		const {count} = this.props.recipes;
		return (
			<div>
				<p>{count} recipes found</p>
				<Pagination defaultActivePage={1} totalPages={Math.round(count/10)} 
				onPageChange={(e,data)=>{
					this.props.handlePage(data.activePage)
					this.props.submitParams()
				}}/>
			</div>
		);
	}
	renderList(){
		if(!this.props.recipes){
			return (
				<div className="ui blue message">
				  <p>Enter food name to look up recipes</p>
				</div>
			);
		}
		const {hits} = this.props.recipes;
		return hits.map(info => {
			return (
				<div className="ui items">
				  <div className="item">
				    <div className="image">
				      <img src={info.recipe.image} alt="Food"/>
				    </div>
				    <div className="content">
				      <a href={info.recipe.url}className="header">{info.recipe.label}</a>
				      <div className="meta">
				        <span>{info.recipe.ingredientLines.map(line=> {
				        	return (
				        		<p>{line}</p>
				        	);
				        })}</span>
				      </div>
				      <div className="description">
				        <p></p>
				      </div>
				      <div className="extra">
				        Additional Details
				      </div>
				    </div>
				  </div>
				</div>
			);
		})
	}
	render(){
		return (
			<div className="ui container">
				<div className="ui stackable grid">
					<div className="four wide column">
						<Accordion/>
					</div>
					<div className="twelve wide column">
						{this.renderCount()}
						{this.renderList()}
					</div>
				</div>
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {recipes: state.search.Recipes}
}
export default connect(mapStateToProps,{handlePage, submitParams})(MealFilter);